import './mock.js'
import s from '../index.js'

s.ssr = ssr

async function ssr(url, fn, attrs) {
  const { view, route } = s._init(url, fn, attrs)
      , mnr = (await import('mithril-node-render')).default
      , head = route.head()

  return Promise.all([
    mnr(view()),
    head ? mnr(route.head()) : ''
  ]).then(([html, head]) => {
    html.slice(0, 2) !== '<!' && (
      html = '<!DOCTYPE html><html><head><meta charset="utf-8"><title></title>'
      + '<meta name="viewport" content="width=device-width,initial-scale=1"></head>'
      + '<body>' + html + '</body></html>'
    )

    return {
      html: html
        .replace(/<title>(.*?)<\/title>/i, '<title>' + (route.title() || '$1') + '</title>')
        .replace('</title>', '</title>' + head + s.bss.ssr()),

      statusCode: route.notFound() ? 404 : 200
    }
  })
}

export default s
